import React from 'react';
import styled from '@emotion/styled';
import { animated, useSpring, config } from 'react-spring';

import { Line } from './Primitive';
import { useFaceParams } from './FaceContext';
import { map } from './utils';

const Hair = styled(Line)`
  stroke-width: 5;
`;

const ggg = v => `translate(0, ${v})`

function Mustache() {
  const y1 = 448;
  const y = 480;
  const openness = 40;
  const { mouthOpen, pitch } = useFaceParams();
  const top = -mouthOpen * 0.25 * openness;
  const bottom = mouthOpen * 0.75 * openness;
  const y2 = map(pitch, -Math.PI * 0.25, Math.PI * 0.25, 10, -10);

  const {t, b} = useSpring({
    t: y2 + top,
    b: y2 + bottom,
    config: config.stiff
  })
  return (
    <>
      <animated.g transform={t.interpolate(ggg)}>
        <Hair x1={-44} x2={-44} y1={y1} y2={y1-4}/>
        <Hair x1={-33} x2={-33} y1={y1} y2={y1-8}/>
        <Hair x1={-22} x2={-22} y1={y1} y2={y1-12}/>
        <Hair x1={-11} x2={-11} y1={y1} y2={y1-20}/>
        <Hair x1={0} x2={0} y1={y1} y2={y1-24}/>
        <Hair x1={11} x2={11} y1={y1} y2={y1-20}/>
        <Hair x1={22} x2={22} y1={y1} y2={y1-12}/>
        <Hair x1={33} x2={33} y1={y1} y2={y1-8}/>
        <Hair x1={44} x2={44} y1={y1} y2={y1-4}/>
      </animated.g>
      <animated.g transform={b.interpolate(ggg)}>
        <Hair x1={-22} x2={-22} y1={y} y2={y+10}/>
        <Hair x1={-11} x2={-11} y1={y} y2={y+10}/>
        <Hair x1={0} x2={0} y1={y} y2={y+10}/>
        <Hair x1={11} x2={11} y1={y} y2={y+10}/>
        <Hair x1={22} x2={22} y1={y} y2={y+10}/>
      </animated.g>
    </>

  );
}

export default Mustache;
