import React from 'react';
import useTimeLoop from './useTimeLoop';
import Perlin from './Perlin';

const noise = new Perlin();

function App() {
  const { time, tick } = useTimeLoop(60);
  //console.log(noise.getValue(time) * 100);
  return (
    <div>
      <div>{'time: '}{time}</div>
      <div>{'tick: '}{tick}</div>
      <svg width={400} height={400}>
        <circle cx={noise.getValue(time/2000) * 100 + 200} cy={noise.getValue(time/1000) * 100 + 200} r="40" stroke="black" stroke-width="3" fill="red" />
      </svg>
    </div>
  );
}

export default App;
