import React from 'react';
import styled from '@emotion/styled';
import { Line } from './Primitive';
import { useFaceParams } from './FaceContext';
import { animated, useSpring, config } from 'react-spring';

import { map } from './utils';

const Hair = styled(Line)`
  stroke-width: 5;
`;

function Beard() {
  const { pitch, mouthOpen } = useFaceParams();
  const y = 568 + mouthOpen * 10;

  const { transform } = useSpring({
    transform: `translate(0, ${y})`,
    config: config.stiff
  })
  return (
    <g style={{ transform: `translateY(${map(pitch, -Math.PI * 0.25, Math.PI * 0.25, 10, -30)}px)` }}>
      <animated.g transform={transform}>
        <Hair x1={-33} x2={-33} y1={-10} y2={-15}/>
        <Hair x1={-22} x2={-22} y1={-5} y2={-20}/>
        <Hair x1={-11} x2={-11} y1={0} y2={-25}/>
        <Hair x1={0} x2={0} y1={0} y2={-30}/>
        <Hair x1={11} x2={11} y1={0} y2={-25}/>
        <Hair x1={22} x2={22} y1={-5} y2={-20}/>
        <Hair x1={33} x2={33} y1={-10} y2={-15}/>
      </animated.g>

    </g>
  );
}

export default Beard;
