import React from 'react';
import Ears from './Ears';
import Outline from './Outline';
import Eyes from './Eyes';
import EyeBrows from './EyeBrows';
import Mouth from './Mouth';
import Nose from './Nose';
import Mustache from './Mustache';
import Beard from './Beard';
import Hair from './Hair';
import FaceContext from './FaceContext'
import { map } from './utils';

function Face({ params }) {
  const { yaw, pitch, roll } = params;
  return (
    <FaceContext.Provider value={params}>
      <svg
        width={240}
        height={320}
        viewBox={`0 0 ${480} ${640}`}
        style={{ transform: `rotateZ(${roll * 180/Math.PI}deg)` }}
      >
        <g style={{
          transform: `translateX(240px)`,
          perspective: 10,
          perspectiveOrigin: `50% 50%`,
          transformOrigin: `0% 75%`,
        }}>
        <Ears />

          <Outline />
          <Hair />
          <g
            style={{

            transform: `
              translateX(${map(yaw, -Math.PI * 0.5, Math.PI * 0.5, -100, 100)}px)
              translateY(${map(pitch, -Math.PI * 0.25, Math.PI * 0.25, -20, 20)}px)
              rotateY(${map(yaw, -Math.PI * 0.5, Math.PI * 0.5, -60, 60)}deg)
            `
          }}>
            <Eyes wideness={120}/>
            <EyeBrows />
            <Mouth />
            <Mustache />
            <Beard />
            <Nose />
          </g>

        </g>

      </svg>
    </FaceContext.Provider>

  );
}
// scaleX(${map(Math.abs(yaw), 0, Math.PI * 0.5, 1, 0.5)})

export { FaceContext }
export default Face;
