import React, { useEffect, useCallback } from 'react';
import styled from '@emotion/styled'
import { Line } from './Primitive';
import { useFaceParams } from './FaceContext';
import { animated, useSpring, config } from 'react-spring';

const Mask = styled(animated.rect)`
  fill: #EDB68C;
`;

const sleep = msec => new Promise(resolve => setTimeout(resolve, msec));

const EyeLine = animated(Line)

function Eyes({
  color = 'black',
  wideness = 40,
  r = 9,
}) {
  // const { eyeOpen } = useFaceParams();

  const cy = 295;
  const rx = r;
  const ry = r;
  const attr = { cy, rx, ry };
  // const lidY = cy + (1.-eyeOpen) * 10 -5;
  const [{ lidY }, set] = useSpring(() => ({
    lidY: cy - 5,
    config: config.stiff
  }))

  /*
  const blink = useCallback(async () => {
    set({ lidY: cy + (1+0.5) * 10 -5 })
    await sleep(100);
    set({ lidY: cy + (1-1) * 10 -5 })
    await sleep(1000);
  }, [])
  */

  useEffect(() => {
    const repeat = async () => {
      while (true) {
        set({ lidY: cy + (1+0.5) * 10 -5 })
        await sleep(100);
        set({ lidY: cy + (1-1) * 10 -5 })
        await sleep(Math.random() * 2000 + 100);
      }
    }
    repeat();

  }, [])
  return (
    <g>
      <ellipse {...attr} cx={(-wideness) * 0.5} fill={color} />
      <ellipse {...attr} cx={(+wideness) * 0.5} fill={color} />
      <Mask x={-80} y={lidY.interpolate(y => y-20)} width={160} height={20}/>
      <EyeLine x1={-50} x2={-50-28} y1={lidY} y2={lidY} />
      <EyeLine x1={50} x2={50+28} y1={lidY} y2={lidY} />
    </g>
  )
}

export default Eyes;
