import body from './body.png';
const noise = new Perlin();

//import { Howl } from 'howler'

const remove = item => {
  return item !== '%' && item !== '*' && item !== '%' && item !== 'ﾟ' && item !== 'ﾞ' && item !== '/' && item !== "'"
}
const sleep = msec => new Promise(resolve => setTimeout(resolve, msec));

const a = ['ｱ','ｶ','ｻ','ﾀ','ﾅ','ﾊ','ﾏ','ﾔ','ﾗ','ﾜ','ｬ'];
const i = ['ｲ','ｷ','ｼ','ﾁ','ﾆ','ﾋ','ﾐ','ﾘ'];
const u = ['ｳ','ｸ','ｽ','ﾂ','ﾇ','ﾌ','ﾑ','ﾕ','ﾙ', 'ｭ'];
const e = ['ｴ','ｹ','ｾ','ﾃ','ﾈ','ﾍ','ﾒ','ﾚ'];
const o = ['ｵ','ｺ','ｿ','ﾄ','ﾉ','ﾎ','ﾓ','ﾖ','ﾛ','ｦ', 'ｮ'];
//const n = [',', ' ', '.'];

const convert = str => {
  if (a.includes(str)) {
    return 1;
  } else if(i.includes(str)){
    return 2;
  } else if(u.includes(str)){
    return 3;
  } else if (e.includes(str)) {
    return 4;
  } else if (o.includes(str)) {
    return 5;
  } else {
    return 6;
  }
}

const client = axios.create({
  baseURL: 'https://api.recaius.jp',
  headers: {
    'Content-Type': 'application/json'
  }
})

