const mouth = [
  { mouthOpen: 0, mouthCorner: 0, mouthWidth: 0.8 },
  { mouthOpen: 0.8, mouthCorner: 0.25, mouthWidth: 0.75 },
  { mouthOpen: 0.25, mouthCorner: -0.5, mouthWidth: 0.8 },
  { mouthOpen: 0.5, mouthCorner: 0.5, mouthWidth: 0.3 },
  { mouthOpen: 0.6, mouthCorner: 0.2, mouthWidth: 0.6 },
  { mouthOpen: 1, mouthCorner: 0.5, mouthWidth: 0.4 },
  { mouthOpen: 0, mouthCorner: 0, mouthWidth: 0.5 },
];

const phrase = [
  'こんにちは、ライフプランナーのタンバタです',
  'おはようございます滝本さん。何かお困りの事はございますか？',
  '先週、車の買い替えを検討されているお客様に、知り合いのディーラーを紹介したところ、気に入った車を見つけられていました。車の買い替えを検討されているかたは、お気軽にご連絡ください。',
  '吾輩は猫である。名前はまだ無い。どこで生れたかとんと見当がつかぬ。何でも薄暗いじめじめした所でニャーニャー泣いていた事だけは記憶している。',
  '日本国民は、正当に選挙された国会における代表者を通じて行動し、われらとわれらの子孫のために、諸国民との協和による成果と、わが国全土にわたって自由のもたらす恵沢を確保し、政府の行為によって再び戦争の惨禍が起こることのないようにすることを決意し、ここに主権が国民に存することを宣言し、この憲法を確定する。'
];

function App() {
  const [index, setIndex] = useState(0);
  const [text, setText] = useState(phrase[0])
  const textArray = useRef();
  /*
  const params = useTweakpane({
    pitch: { value: -7, min: -10, max: 10, step: 0.1 },
    depth: { value: 1, min: -4, max: 4, step: 0.1 },
    volume: { value: 0, min: -50, max: 50, step: 1 },
    upower: { value: -10, min: -10, max: 10, step: 0.1 },
    speed: { value: 2, min: -10, max: 10, step: 0.1 },
    happy: { value: 0, min: 0, max: 200, step: 1 },
    angry: { value: 0, min: 0, max: 200, step: 1 },
    sad: { value: 0, min: 0, max: 200, step: 1 },
    fear: { value: 0, min: 0, max: 200, step: 1 },
    tender: { value: 0, min: 0, max: 200, step: 1 },
  })*/

  const params = {
    pitch: -7,
    depth: 1,
    upower: -10,
    speed: 2,
  }

  const audio = useRef();
  const duration = useRef(1000);
  const [token, setToken] = useState(null);
  const [ready, setReady] = useState(false);
  useEffect(() => {

    client.post('auth/v2/tokens', {
      speech_synthesis: {
        service_id: 'jarashi0126_MAYp2S91vU',
        password: 'sukiyaki0512'
      }
    })
    .then(response => {
      console.log('Success')
      console.log(response.data)
      setToken(response.data.token)
    })
    .catch(error => {
      console.log('Failure')
      console.log(error.response.config)
      console.log(error.response.data)
    })
    .then(() => {
      console.log('Complete')
    })

  }, [])

  const mouthChanger = useCallback(async (arr, duration) => {
    let couner = 0;
    while (couner < arr.length) {
      // console.log(arr[couner]);
      setIndex(arr[couner]);
      await sleep(duration/(arr.length+1));
      couner++;
    }
    setIndex(0);
  }, [])
//
  const speak = useCallback(() => {
    console.log(token);

    axios.post(
      'https://api.recaius.jp/tts/v2/plaintext2phonetictext',
      {
        plain_text: text,
        //'先週、車の買い替えを検討されているお客様に、知り合いのディーラーを紹介したところ、気に入った車を見つけられていました。',

        //'車の買い替えを検討されている方は、お気軽にご連絡ください。',

        //
        lang: 'ja_JP',
        //speaker_id: 'ja_JP-F0006-C53T',
        //codec: 'audio/wav',
        //encoding: 'utf-8'
        //response_type: 'binary'
      },
      {
        headers: {
          'Content-Type': 'application/json',
          'X-Token': token
        }
      }
    )
    .then(response => {
      console.log('Success')
      console.log(response.data)

      const text = response.data;
      //"ｺﾝﾆﾁﾜ,ﾗｲﾌ%ﾌﾟﾗ'ﾝﾅｰﾉ ﾀ*ﾝﾊﾞﾀﾃﾞｽ%.";
      let filtered = text.split('').filter(remove);
      filtered = filtered.map((item, i) => {
        if(item === 'ｰ') {
          return filtered[i-1]
        } else {
          return item;
        };
      })
      const converted = filtered.map(convert);
      const str = converted.join(',');
      console.log(str);
      const final = str.replace( /6/g , "666" ).split(',');
      console.log(final);
      textArray.current = converted;

      axios.post(
        'https://api.recaius.jp/tts/v2/phonetictext2speechwave',
        {
          phonetic_text: response.data,
          lang: 'ja_JP',
          speaker_id: 'ja_JP-M0001-H00T',
          tag_mode: 1,
          ...params,
          codec: 'audio/mpeg'
        },
        {
          headers: {
            'Content-Type': 'application/json',
            'X-Token': token,
          },
          responseType: 'arraybuffer',
          responseEncoding: 'binary'
        }
      )
      .then(respons => {
        console.log(respons);



        var uInt8Array = new Uint8Array(respons.data);
        var blob = new Blob([uInt8Array.buffer], { type: 'audio/mpeg' });
        audio.current.src = URL.createObjectURL(blob);
        audio.current.type = 'audio/mpeg';
        //audio.current.play();
        /*
        const sourceElement = document.createElement('source');


          audio.current.appendChild(sourceElement);
          sourceElement.src = URL.createObjectURL(blob);
          sourceElement.type = 'audio/x-linear';
          */


        audio.current.addEventListener("loadedmetadata", function() {
          console.log(audio.current.duration);
          duration.current = (audio.current.duration -0.8) * 1000; //* 0.9
          //audio.current.play();
          mouthChanger(textArray.current, duration.current);
          setReady(true);

        }, true);
        // audio.current.play();
      })

    })
    .catch(error => {
      console.log('Failure')
      //console.log(error.response.config)
      console.log(error)
    })
  }, [params, token, text])
  const { time } = useTimeLoop();

  const r = {
    yaw: noise.getValue(time/4000) * Math.PI * 0.125,
    pitch: noise.getValue(time/1000) * Math.PI * 0.125,
    roll: noise.getValue(time/6000) * Math.PI * 0.125 * 0.25
  }
  const replay = useCallback(() => {
    audio.current.play();
    mouthChanger(textArray.current, duration.current);
  }, [])
  return (
    <Container>
      <Canvas>
        <Person>
          <Face params={{ ...r, ...mouth[index] }} />
          <Body />
        </Person>
      </Canvas>
      <Input>
        <Textarea
          value={text}
          onChange={e => {
            setText(e.target.value);
            setReady(false)
          }}
        />
        <Tab>
          {phrase.map((p, i) => {
            return <Item key={i} onClick={() => {
              setText(p);
              setReady(false);
            }} focused={text === p}>{i+1}</Item>
          })}
        </Tab>
        <Button onClick={() => {
          console.log(document.documentElement);
          document.documentElement.webkitRequestFullscreen();
        }}>フルスクリーン</Button>
        <Button onClick={ready ? replay : speak}>しゃべる</Button>
        <Button onClick={() => audio.current.play()}>しゃべる</Button>
      </Input>
      <audio ref={audio} />
    </Container>
  );
}


