import React from 'react';
import styled from '@emotion/styled';
import { useFaceParams } from './FaceContext';
import { Path } from './Primitive';
import { map } from './utils';

const Ellipse = styled.ellipse`
  fill: #EDB68C;
  stroke: black;
  stroke-width: 6;
`

const Outline = styled(Path)`
  fill: #EDB68C;
`;

function Eairs() {
  const { yaw, pitch } = useFaceParams();

  const cy = 345;
  const rx = 30;
  const ry = 60;
  // const attr = { cy, rx, ry };
  const d = `
    M ${-30} ${-60}
    Q ${0} ${-55} ${25} ${-55}
    Q ${25} ${-30} ${30} ${0}
    Q ${30} ${45} ${-10} ${60}
    L ${-30} ${60}
    Z
  `

  // console.log();


  return (
    <g style={{ transform: `translateY(${map(pitch, -Math.PI * 0.25, Math.PI * 0.25, 20, -20)}px)` }}>
      <Outline
        transform={`translate(${Math.sin(yaw + Math.PI * 0.6) * 160}, ${cy})`}
        d={d}
      />
      <Outline
        transform={`translate(${Math.sin(yaw - Math.PI * 0.6) * 160}, ${cy}) scale(-1, 1)`}
        d={d}
      />

      {/* <Ellipse {...attr} cx={Math.sin(yaw - Math.PI * 0.6) * 160} /> */}
    </g>
  )
}

export default Eairs;
