import React from 'react';
import { Path } from './Primitive';
import { useFaceParams } from './FaceContext';
import { map } from './utils';

function Nose() {
  const width = 46;
  const { yaw, mouthOpen } = useFaceParams();
  const y = 400;// - mouthOpen * 5;

  return (
    <g style={{ transform: `translateX(${map(yaw, -Math.PI * 0.5, Math.PI * 0.5, -50, 50)}px)` }}>
      <Path d={`
        M -${width * 0.5} ${y}
        L ${map(yaw, -Math.PI * 0.5, Math.PI * 0.5, -40, 40)} ${y + 10}
        L ${width * 0.5} ${y}
      `}
    />
    </g>
  )
}
export default Nose;
