import React from 'react';
import styled from '@emotion/styled';
import { Path } from './Primitive';

const Brow = styled(Path)`
  stroke-width: 8.5;
`

function EyeBrows() {
  const y = 258;

  return (
    <g>
      <Brow d={`
        M -${30} ${y}
        Q -${90-10} ${y - 10} -${90} ${y - 10}
        Q -${90+10} ${y - 10} -${(30+95)} ${y}
      `} />
      <Brow d={`
        M ${30} ${y}
        Q ${90-10} ${y - 10} ${90} ${y - 10}
        Q ${90+10} ${y - 10} ${(30+95)} ${y}
      `} />
    </g>
  )
}

export default EyeBrows;
