import React from 'react';
import styled from '@emotion/styled';
import { animated, useSpring, config } from 'react-spring';

import { Path } from './Primitive';
import { useFaceParams } from './FaceContext';
import { map } from './utils';

const Line = styled(animated(Path))`
  fill: #EDB68C;
`;

function Outline() {
  const { yaw, pitch, mouthOpen } = useFaceParams();
  const ratio = map(Math.abs(pitch), 0, Math.PI * 0.25, 1, 0.95);
  const height = 500 * ratio;
  const width = 300;
  const bottomY = height * 0.5 + mouthOpen * 10;
  const right = width * 0.5 - 20 * Math.min(yaw, 0);
  const left = -width * 0.5 - 20 * Math.max(yaw, 0);
  const { d } = useSpring({ d: `
    M 0 -${(height) * 0.5}
    Q ${right * 0.8} -${(height) * 0.5} ${right} ${(height) * -0.2}
    Q ${right} ${(height) * 0} ${right * 0.8} ${(height) * 0.25}
    Q ${width * 0.2} ${bottomY} ${map(yaw, -Math.PI*0.5, Math.PI*0.5, -100, 100)} ${bottomY}
    Q -${width * 0.2} ${bottomY} ${left * 0.8} ${(height) * 0.25}
    Q ${left} ${(height) * 0} ${left} ${(height) * -0.2}
    Q ${left * 0.8} -${(height) * 0.5} 0 -${(height) * 0.5}
    Z
  `, config: config.stiff })
  return (
    <g style={{ transform: `translateY(320px)` }}>
      <Line
        id={'outline'}
        d={d}
      />
    </g>
  )
}

//    <ellipse rx={150} ry={250} cx={0} cy={320} fill={color} stroke={'black'} strokeWidth={6} />


export default Outline;
