export const lerp = (start, stop, amt) => start + (stop-start) * amt;
export const norm = (value, start, stop) => (value - start) / (stop - start);
export const map = (value, start1, stop1, start2, stop2) => lerp(start2, stop2, norm(value, start1, stop1));
export const clamp = (value, min, max) => Math.min(Math.max(min, value), max);
