import React from 'react';
import styled from '@emotion/styled';
import { animated, useSpring, config } from 'react-spring';
import { Path } from './Primitive';
import { useFaceParams } from './FaceContext';
import { map } from './utils';

const Fill = styled(animated(Path))`
  fill: #D84747;
`;

function Mouth() {
  const openness = 40;
  const { mouthOpen, mouthCorner, mouthWidth, yaw, pitch } = useFaceParams();
  const wideness = 140 * mouthWidth;
  const centerX = map(yaw, -Math.PI * 0.5, Math.PI * 0.5, -wideness * 0.5, wideness * 0.5);
  const y = 464;
  const top = y - mouthOpen * 0.25 * openness;
  const bottom = y + mouthOpen * 0.75 * openness;
  const side = wideness * 0.5 * map(mouthOpen, 0, 1, 1, 0.75);
  const y2 = map(pitch, -Math.PI * 0.25, Math.PI * 0.25, 10, -10);
  const { d } = useSpring({ d: `
    M ${centerX} ${top}
    Q ${side*0.5} ${top} ${side} ${y+ mouthCorner * 10}
    Q ${side*0.5} ${bottom} ${centerX} ${bottom}
    Q -${side*0.5} ${bottom} -${side} ${y+ mouthCorner * 10}
    Q -${side*0.5} ${top} ${centerX} ${top}
    Z
  `, config: { duration: 100 } })
  return (
    <Fill
      transform={`translate(0, ${y2})`}
      d={d}
    />
  );
}

// <Line  x1={-70} x2={70} y1={y} y2={y} />


export default Mouth;
