import { useEffect, useState, useRef } from 'react';
import raf from "raf";

function useTime(refreshRate = 60) {
  const [{ time, tick }, setTime] = useState({ time: 0, tick: 0 });
  const r = useRef();
  useEffect(() => {
    let startTime, lastTime;
    let interval = 1000 / refreshRate;
    lastTime = -interval;
    const loop = (t) => {
      r.current = raf(loop);
      if (!startTime) startTime = t;
      //if (t - lastTime > interval) {
      if (t - lastTime > interval) {
        lastTime = t;
        setTime(current => {
          return {
            time: t - startTime,
            tick: current.tick + 1,
          }
        });
      }
    };
    r.current = raf(loop);
    return () => { raf.cancel(r.current); }
  }, []);
  return { time, tick };
}

export default useTime;
