import { useState, useEffect, useMemo, useRef } from 'react';
import Tweakpane from 'tweakpane';


function useTweakpane(initialState, show = true) {

  const el = useRef();
  const isShow = useRef(show);

  const defaultValues = useMemo(() =>
    Object.keys(initialState).reduce(
      (o, c) => {
        return ({...o, [c]: initialState[c].value})
      },
      {}
    )
  , []);
  const [params, setParams] = useState(defaultValues);

  useEffect(() => {

    const pane = new Tweakpane();
    Object.keys(initialState).forEach((key) => {
      const config = initialState[key];
      delete config.value;
      pane.addInput(defaultValues, key, config).on('change', value => setParams(state => ({ ...state, [key]: value })));
    });

    el.current = pane.element;
    el.current.style.visibility = isShow.current ? 'visible' : 'hidden';
    const handleKeyDown = e => {

      if(e.keyCode === 69) {
         el.current.style.visibility = isShow.current ? 'hidden' : 'visible';
         isShow.current = !isShow.current;
      }
    }
    window.addEventListener('keyup', handleKeyDown);
  }, [])

  return params;
}

export default useTweakpane;
